.controls{
    position: absolute;
    top: 45%;
    left:0px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    margin: 0;
}
.controls button{
    background-color: rgba(0,0,0,0.05);
    border: 0;
    display: block;
    margin: 5px 0;
    padding: 16px;
    width: 100%;
    border-radius: 18px;
}
